<template>
  <div>
    <nav class="navbar navbar-dark fixed-top bg-dark flex-md-nowrap p-0 shadow">
      <a class="navbar-brand font-weight-bold col-sm-3 col-md-2 mr-0 text-white">Login</a>
    </nav>
    <div class="container vw-100 vh-100 c-container-color col-12">
      <form class="c-position-absolute col-xl-3 col-lg-6
        col-md-8 col-sm-12 rounded shadow bg-light" @submit.prevent="login">
        <h4 class="mt-5 text-center font-weight-bold">Login Page</h4>
        <div class="form-group">
          <label for="userID-input">Account</label>
          <input
            class="form-control"
            id="userID-input"
            placeholder="Account"
          />
        </div>
        <div class="form-group">
          <label for="password-input">Password</label>
          <input type="password" class="form-control" id="password-input" placeholder="Password" />
        </div>
        <div class="form-group form-check">
          <input type="checkbox" class="form-check-input" id="remember" />
          <label class="form-check-label" for="remember">Remember me</label>
        </div>
        <button type="submit" id="btn-login"
          class="btn btn-primary mb-5">Confirm</button>
      </form>
    </div>
  </div>
</template>

<script>

export default {
  methods: {
    login() {
      const vm = this;
      const userID = document.getElementById('userID-input').value;
      const password = document.getElementById('password-input').value;
      const cors = 'https://cors-anywhere.herokuapp.com/';
      const url = 'http://18.223.16.57/api/v1/auth/login';
      const xhr = new XMLHttpRequest();
      const params = { loginProvider: 'Local', providerUserID: userID, password: password };
      vm.$store.dispatch('loadingOverlay');
      if (userID && password) {
        xhr.open('post', cors + url);
        xhr.setRequestHeader('Content-Type', 'application/json;charset=UTF-8');
        xhr.send(JSON.stringify(params));
        xhr.onloadend = () => {
          if (JSON.parse(xhr.response).code === 200) {
            localStorage.setItem('token', JSON.stringify({ token: JSON.parse(xhr.response).data }));
            vm.$router.push({ path: 'panel' });
            vm.$store.dispatch('loadingOverlay');
          } else {
            vm.$store.dispatch('loadingOverlay');
            // eslint-disable-next-line
            alert('Wrong ID or password');
          }
        };
      } else {
        vm.$store.dispatch('loadingOverlay');
        // eslint-disable-next-line
        alert('Please Enter your ID and password');
      }
    },
  },
};
</script>
